import React from "react";
import "./style.css";


function ImagesCard(props) {
    return (
        <div className="row">
            <div className="col-12">
                <div className="card">
                    <div className="content">
                        <ul>
                            <button className="btn">
                            <img alt={"Click me I'm " + props.name} src={props.image} className="text-nowrap" />
                            </button>
                        </ul>
                    </div>
                </div>
                </div>
            </div>
    )
};

export default ImagesCard;